@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap");
.main_window {
  height: 100%;
  min-width: 320px;
  position: relative;
}

.main_window .step1 {
  width: 150px;
  height: 3px;
  position: relative;
  top: 50px;
  left: 70px;
  background: black;
  -webkit-animation-name: draw;
          animation-name: draw;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes draw {
  from {
    width: 0px;
  }
  to {
    width: 150px;
  }
}

@keyframes draw {
  from {
    width: 0px;
  }
  to {
    width: 150px;
  }
}

.main_window .step2 {
  width: 3px;
  height: 55px;
  position: relative;
  top: 49px;
  left: 217px;
  background: black;
  -webkit-animation-name: draw2;
          animation-name: draw2;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes draw2 {
  from {
    height: 0px;
  }
  to {
    height: 55px;
  }
}

@keyframes draw2 {
  from {
    height: 0px;
  }
  to {
    height: 55px;
  }
}

.main_window .step3 {
  width: 58px;
  height: 55px;
  position: relative;
  top: 49px;
  left: 186px;
  border-radius: 50%;
  border: 3px solid;
}

.main_window .step4 {
  width: 3px;
  height: 55px;
